import React from "react";
import Redrecord from "./Redrecord";
import "./Record.css";
import Redrecord1 from "./Redrecord1";
const Record = ({ biddatadice, period1 }) => {
  return (
    <>
      <div>
        {/*  here is 1 file  circles*/}
        <div className="center-record">
          <Redrecord1 bgcolor={"orange"} numresult={"?"} period={period1} />
          {biddatadice &&
            biddatadice
              .reverse()
              .slice(0, 30)
              .map((item) => {
                return (
                  <>
                    <div style={{ width: "3px" }}></div>

                    <Redrecord
                      bgcolor={"rgb(80, 81, 114)"}
                      numresult={item.result_dice}
                      period={item.period}
                    />
                  </>
                );
              })}
          <div style={{ width: "3px" }}></div>
        </div>
      </div>
    </>
  );
};

export default Record;
