import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Fade from "@material-ui/core/Fade";
import "./DiceParity.css";
import RecordTap from "./record/RecordTap";
import OrderTap from "./order/OrderTap";
import Timer from "./Timer";
import Bidcom from "../Bidcom";
import ReactSlider from "react-slider";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    "&:focus": {
      outline: "none",
    },
  },

  paper: {
    backgroundColor: theme.palette.background.paper,
    display: "flex",
    border: "none",
    flexDirection: "column",
    alignItems: "center",
    boxShadow: theme.shadows[5],

    "&:focus": {
      outline: "none",
    },

    position: "relative",
    borderRadius: "15px",
  },
}));
const DiceParity = ({
  data,
  setcreategame,
  creategam,
  gettimedice,
  biddatadice,
  bidhistorydice,
  resultdice,
  disable,
  setdisable,
  resulthistorydice,
}) => {
  const classes = useStyles();
  const [refetch, setrefetch] = useState(false);
  const [bidvalue, setbidvalue] = useState("");
  const [color, setcolor] = useState("");
  const [open, setOpen] = useState(false);
  const [period, setperiod] = useState("");
  const [dicetype, setdicetype] = useState("dice");
  const [value, setValue] = useState(50);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  console.log(disable);
  useEffect(() => {
    setperiod(data ? data.period : "");
  }, [refetch, disable, data]);

  return (
    <>
      <div>
        <Modal
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={open}>
            <div className={classes.paper}>
              <Bidcom
                bidvalue={bidvalue}
                dicetype={dicetype}
                period={data ? data.period : ""}
                setOpen={setOpen}
                bidhistorydice={bidhistorydice}
              />
            </div>
          </Fade>
        </Modal>
      </div>
      <div className="allcenter">
        <div className="scrollbar-indiv">
          <div className="cenperiod">
            <div>
              <p className="per">Period</p>
              <p className="pernum">{data ? data.period : ""}</p>
            </div>
            <div>
              <p className="Cout">Count Down</p>
              <Timer
                startingtime={data.startTime}
                currenttime={data.currentTime}
                setcreategam={setcreategame}
                creategam={creategam}
                gettimedice={gettimedice}
                setrefetch={setrefetch}
                refetch={refetch}
                setdisable={setdisable}
                onTimeOut={() => alert("ok")}
                resulthistorydice={resulthistorydice}
              />
            </div>
          </div>
          <div className="center-nav">
            <div className="card mb-4 shadow">
              <div className="dice-stat flex py-3">
                <div className="box flex ">
                  <h2 style={{ fontSize: "14px", margin: "0px" }}>Less than</h2>
                  <h2 style={{ fontSize: "30px" }}>{value}</h2>
                </div>
                <div className="mb-2">|</div>
                <div className="box flex">
                  <h2 style={{ fontSize: "14px" }}>Multiplier</h2>
                  <h2 style={{ fontSize: "30px" }}>1.06</h2>
                </div>
              </div>
            </div>
          </div>

          <div className="center-nav mt-2">
            <ReactSlider
              className="horizontal-slider mb-4"
              marks
              markClassName="mark"
              min={1}
              max={100}
              thumbClassName="thumb"
              trackClassName="track"
              minDistance={8}
              renderThumb={(props, state) => {
                setValue(state.valueNow);

                return (
                  <>
                    <div {...props}>
                      <div className="tooltip" role="slider">
                        {value}
                      </div>
                    </div>
                  </>
                );
              }}
            />
          </div>

          <div className="flex range mb-4">
            <h6>1</h6>
            <h6>25</h6>
            <h6>50</h6>
            <h6>75</h6>
            <h6>100</h6>
          </div>
          <div>
            <button
              onClick={() => {
                setbidvalue(value);
                handleOpen();
                setcolor("");
              }}
              disabled={disable ? true : false}
              className="btn btn-primary py-2 mb-5"
            >
              Less than {value}
            </button>
          </div>
          <RecordTap biddatadice={resultdice} period1={period} />
          <OrderTap biddatadice={biddatadice} />
        </div>
      </div>
    </>
  );
};

export default DiceParity;
